<template>
  <div class="container">
      <div class="sort">Сортировка:</div>
      <div class="filter" data-sort="date" @click="stateBtnChange">Дата регистрации</div>
      <div class="filter" data-sort="rating" @click="stateBtnChange">Рейтинг</div>
  </div>
</template>

<script>
export default {
  methods: {
    stateBtnChange() {
      [...document.querySelectorAll('.filter')].map(el => el.classList.remove('active'));
      event.target.classList.add('active');

      let dataSort = event.target.getAttribute('data-sort');

      this.$emit('filter', dataSort);

      if (dataSort === 'date' || dataSort === 'rating')
        event.target.setAttribute('data-sort', dataSort+'Reverse');

      if (dataSort === 'dateReverse' || dataSort === 'ratingReverse')
        event.target.setAttribute('data-sort', dataSort.replace('Reverse', ''));
    }
  }
}
</script>

<style scoped>
.container {
  color: #b3bdc3;
  display: flex;
  margin: 50px 0 20px 1%;
}

.filter {
    margin-left: 10px;
    cursor: pointer;
    user-select: none;
}

.active {
    color: black;
    border-bottom: 1px dashed black;
  }
</style>