<template>
  <div id="app">
    <h1 class="heading">Cписок пользователей</h1>
    <div class="header">
      <label class="input">
        <svg width="1em" height="1em" viewBox="0 0 16 16" class="bi bi-search" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fill-rule="evenodd" d="M10.442 10.442a1 1 0 0 1 1.415 0l3.85 3.85a1 1 0 0 1-1.414 1.415l-3.85-3.85a1 1 0 0 1 0-1.415z"/>
          <path fill-rule="evenodd" d="M6.5 12a5.5 5.5 0 1 0 0-11 5.5 5.5 0 0 0 0 11zM13 6.5a6.5 6.5 0 1 1-13 0 6.5 6.5 0 0 1 13 0z"/>
        </svg>
        <input type="text" v-model="searthline">
      </label>
      <ClearFilters 
        @click="filter = 'all';
        start = 0;
        end = 5"
      />
    </div>
    <div class="menu">
      <Filters @filter="filterChanged"/>
      <div class="page-menu">
        <div class="next" @click="pageUp()">Вперёд</div>
        <div class="back" @click="pageDown()">Назад</div>
      </div>
    </div>
    <Table :pageUpdate="pageUpdate()" @remove="remove"/>
  </div>
</template>

<script>
import Filters from '@/components/Filters'
import Table from '@/components/Table'
import ClearFilters from '@/components/ClearFilters'


export default {
  name: 'App',
  data() {
    return {
      users: [],
      filter: 'all',
      searthline: '',
      start: 0,
      end: 5
    }
  },
  components: {
    ClearFilters,
    Filters, 
    Table
  },
  computed: {
    filters() {
      if (this.filter === 'all') {
        // return this.users;
        return this.searth();
      }

      if (this.filter === 'date') {
        return this.searth().slice().sort((a, b) => new Date(a.registration_date) - new Date(b.registration_date));
      }

      if (this.filter === 'dateReverse') {
        return this.searth().slice().sort((a, b) => new Date(a.registration_date) - new Date(b.registration_date)).reverse();
      }

      if (this.filter === 'rating') {
        return this.searth().slice().sort((a, b) => a.rating - b.rating);
      }

      if (this.filter === 'ratingReverse') {
        return this.searth().slice().sort((a, b) => a.rating - b.rating).reverse();
      }
    }
  },
  methods: {
    remove(id) {
      if (confirm('Удалить?')) 
        this.users.splice(this.users.findIndex(el => el.id === id), 1);
    },

    filterChanged(filter) {
      this.filter = filter;
    },

    searth() {
      let name = this.users.filter(el => el.username.toLowerCase().includes(this.searthline.toLowerCase()));
      let email = this.users.filter(el => el.email.toLowerCase().includes(this.searthline.toLowerCase()));
      return [...new Set(name.concat(email))];
    },

    pageUp() {
      if (this.end <= this.filters.length - 1) {
        this.start += 5;
        this.end += 5;
        this.pageUpdate();
      } else {
        this.start = 0;
        this.end = 5;
      }
    },

    pageDown() {
      if (this.start !=  0) {
        this.start -= 5;
        this.end -= 5;
        this.pageUpdate();
      } else {
        this.start = this.filters.length - 5;
        this.end = this.filters.length;
      }
    },

    pageUpdate() { 
      return this.filters.slice(this.start, this.end);
    }  
  },
  
  mounted() {
    fetch('https://5ebbb8e5f2cfeb001697d05c.mockapi.io/users')
      .then(response => response.json())
      .then(json => { this.users = json});
  }
}
</script>

